*{
    font-family: Helvetica;
    
}

body{
    background-color: #262626;
    color: #fff;
    padding: 60px;
}

input{
    border-radius: 25em;
    color: grey;
    font-size: 1.3rem;
    padding: 10px 5px;
    width: 100%;
}
input:focus{
    outline: none;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p{
    padding: 5px;
    text-align: center;
}
.card img{
    max-height: 170px;
}